html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* Global */
body {
  background-color: #EBE7DE;
  color: #ffffff;
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
  margin-inline: auto;
}

h1,
h2,
.text-white,
h3 {
  margin: 0 0 5rem 0;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .text-white {
  font-size: 3.6rem;
  color: #5E311E;
}
h2 span, .text-white span {
  color: #C39C63;
  font-size: 1.8rem;
  display: block;
}
h2::after, .text-white::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-repeat: no-repeat;
  background-size: 10rem;
}

.text-white {
  color: #ffffff;
}
.text-white span {
  color: #ffffff;
}
.text-white::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

p {
  font-size: 1.6rem;
  line-height: 2;
  color: #000000;
}

a {
  text-decoration: none;
  color: #ffffff;
}

li {
  list-style: none;
}

ul {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  /* para que no se muestre con un espacio abajo */
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  position: absolute;
  background-color: #000000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.6;
}
.header--home {
  background-image: url("../img/header_inicio.jpg");
}
.header--about {
  background-image: url("../img/header_nosotros.jpg");
}
.header--process {
  background-image: url("../img/header_proceso.jpg");
}
.header--menu {
  background-image: url("../img/header_menu.jpg");
}
.header--gallery {
  background-image: url("../img/header_galeria.jpg");
}
.header--contact {
  background-image: url("../img/header_contacto.jpg");
}
.header__content {
  position: relative;
}
.header__text h1 {
  margin-top: 5rem;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__text h1 {
    margin-top: 10rem;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.navbar__logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .navbar__logo {
    width: 20rem;
    margin: 0;
  }
}
.navbar__menu {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navbar__menu {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.navbar__menu a {
  text-align: center;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navbar__menu a {
    margin-bottom: 0;
  }
}
.navbar__menu a:hover, .navbar__menu a.active {
  color: #C39C63;
}

.footer {
  background-color: #5E311E;
}
.footer__content {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__box {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer__box {
    margin-bottom: 0;
  }
}
.footer__box h3 {
  font-family: "Bona Nova", serif;
  color: #C39C63;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
}
.footer__box p,
.footer__box a {
  font-size: 2rem;
  color: #000000;
  margin: 0;
}
.footer .copyright {
  text-align: center;
  margin: 0;
  color: #ffffff;
  padding: 1.5rem;
  font-family: "Bona Nova", serif;
  font-size: 2rem;
}

.main-content {
  background-color: #ffffff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .main-content {
    position: relative;
    padding: 10rem;
    margin-top: -10rem;
  }
}

.icons {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .icons__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icons__list li img {
  margin: 0 auto;
  width: 12rem;
}
.icons__list li p {
  font-family: "Bona Nova", serif;
  font-size: 3.2rem;
  color: #5E311E;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery__image img {
  width: 30rem;
  height: 30rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .gallery__image:nth-child(1) {
    grid-row: span 2;
  }
  .gallery__image:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .gallery__image:nth-child(2) {
    grid-column: span 2;
  }
  .gallery__image:nth-child(2) img {
    width: 62rem;
    object-position: center;
  }
  .gallery__image:nth-child(4) {
    grid-row: span 2;
  }
  .gallery__image:nth-child(4) img {
    height: 62rem;
    object-position: center;
  }
  .gallery__image:nth-child(5) {
    grid-column: 3/5;
  }
  .gallery__image:nth-child(5) img {
    width: 62rem;
    object-position: center;
  }
  .gallery__image:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery__image:nth-child(6) img {
    width: 62rem;
    object-position: center;
  }
  .gallery__image:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery__image:nth-child(7) img {
    height: 62rem;
    object-position: center;
  }
  .gallery__image:nth-child(8) {
    grid-column: span 3;
  }
  .gallery__image:nth-child(8) img {
    width: 94rem;
    object-position: center;
  }
  .gallery__image:nth-child(9) {
    grid-column: span 2;
  }
  .gallery__image:nth-child(9) img {
    width: 62rem;
    object-position: center;
  }
  .gallery__image:nth-child(10) {
    grid-column: span 2;
  }
  .gallery__image:nth-child(10) img {
    width: 62rem;
    object-position: center;
  }
}

.menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #5E311E, #C39C63);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, right;
  background-size: 200%;
  padding: 15rem 2rem 20rem 2rem;
}
@media (min-width: 768px) {
  .menu {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .menu {
    background-size: 50%, 50%, 100%;
  }
}

@media (min-width: 768px) {
  .menu__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.menu__content h3 {
  color: #ffffff;
}
.menu__content li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu__content li p {
  color: #ffffff;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}
.menu__content li p span {
  display: block;
  color: #C39C63;
}
.menu__content li .price {
  font-size: 4rem;
  font-weight: 700;
}

.testimonials {
  padding: 10rem 2rem;
}
@media (min-width: 768px) {
  .testimonials {
    padding: 10rem;
  }
}
.testimonials .testimonial {
  background-color: #EBE7DE;
  padding: 2rem 4rem;
  margin: 0 auto;
  max-width: 50rem;
  border-radius: 1rem;
}
.testimonials .testimonial .author {
  text-align: right;
  font-size: 2.4rem;
  color: #5E311E;
  font-family: "Bona Nova", serif;
}

@media (min-width: 768px) {
  .about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}
.about__image {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .about__image {
    margin: 0 2rem 0 0;
  }
}
.about__image img {
  transform: rotate(-6deg);
  border: 3rem solid #ffffff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}
.about__text blockquote {
  margin: 0;
  padding: 1rem;
  color: #5E311E;
  border-left: 0.5rem solid #5E311E;
  font-size: 2rem;
}

.coffee-process {
  margin-bottom: 5rem;
}
.coffee-process:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .coffee-process {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
}
.coffee-process:nth-of-type(even) .image {
  order: 1;
}
.coffee-process .image,
.coffee-process .text {
  flex: 1;
}
.coffee-process .text {
  margin-top: 1rem;
}
.coffee-process .text h3 {
  margin: 0;
  text-align: left;
  color: #5E311E;
}
.coffee-process .text h3::after {
  content: "";
  display: block;
  width: 50%;
  height: 5px;
  background-color: #C39C63;
  margin: 1rem 0;
}

.menu-content {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/5;
}
@media (min-width: 480px) {
  .menu-content {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 768px) {
  .menu-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.menu-content .text p {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contact-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(../img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.form__field {
  margin-bottom: 2rem;
}
.form__field:last-child {
  margin-bottom: 0;
}
.form__field label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
  color: #000000;
}
.form__field input {
  color: #d0d5d8;
}
.form__field input[type=submit] {
  background-color: #C39C63;
  width: 100%;
  border: none;
  padding: 1rem;
  color: #ffffff;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
}
.form__field input[type=submit]:hover {
  background-color: #bc9051;
}
.form__field input:not([type=submit]) {
  margin-top: 1rem;
  width: 100%;
  outline: none;
  border: none;
  padding: 1rem;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
  border-bottom: 2px solid #ECEEEF;
  background-color: transparent;
  text-transform: lowercase;
}
.form__field input:not([type=submit]):focus {
  border-bottom: 2px solid #C39C63;
}
.form__field input:not([type=submit])::-webkit-input-placeholder {
  color: #d0d5d8;
}
.form__field select {
  margin-top: 1rem;
  width: 100%;
  outline: none;
  border: none;
  padding: 1rem;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
  border-bottom: 2px solid #ECEEEF;
  background-color: transparent;
  text-transform: lowercase;
  color: #d0d5d8;
}
.form__field select:focus {
  border-bottom: 2px solid #C39C63;
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #5E311E;
}

.mt--10 {
  margin-top: -10rem;
}

.bg-white {
  background-color: #ffffff;
}

.pb-0 {
  padding-bottom: 0;
}