@use '../general/variables' as v;
@use '../general/mixins' as m;

.menu {
    background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, v.$primary, v.$secondary);
    // linear-gradient(to top, v.$primary, v.$primary);
    background-repeat: no-repeat;
    background-position: center 5rem, center 92%, right;
    background-size: 200%;
    padding: 15rem 2rem 20rem 2rem;

    @include m.tablet {
        padding: 15rem 0 20rem 0;
        background-size: 100%;
    }

    @include m.desktop {
        background-size: 50%, 50%, 100%;
    }
}

.menu__grid {

    @include m.tablet {
        @include m.grid(2, 2rem);
    }
}

.menu__content {
    
    h3 {
        color: v.$white;
    }

    li {
        display: flex;
        justify-content: space-between;
        align-items: center;

        p {
            color: v.$white;
            font-size: 2.2rem;
            line-height: 1.5;
            margin: 0 0 2rem 0;

            span {
                display: block; // Forzar el salto de línea
                color: v.$secondary;
            }
        }

        .price {
            font-size: 4rem;
            font-weight: 700;
        }
    }
}